<script>
/* 表单状态formState: 0预览，1新建，2编辑，3查看 */

import ReferenceSearchTable from "@/views/system/field-widget/reference-search-table";
import FormState from "@/views/system/form-state-variables";
import {
  isDefinedNull,
  isNull,
  isNotNull,
  isEmptyStr,
  arrayContain,
} from "@/utils/util";
// import * as SYS from "@/api/system-manager";
import { baseURL } from "@/config";
// import { listRole } from "@/api/user";
import SubForm from "@/views/system/field-widget/form-widget";
import {
  initDataList,
  createRecord,
  updateRecord,
  saveRecord,
  deleteRecord,
  getDataList,
  getColumnShowNoPage,
} from "@/api";
import Vue from "vue";
import { createLayoutObj } from "@/views/system/layout/form-layout-object";
export default {
  name: "FieldWidget",
  components: { ReferenceSearchTable, SubForm },
  props: {
    crudMethods: {
      type: Object,
      default: () => {
        return {
          initDataList: initDataList,
          createRecord: createRecord,
          updateRecord: updateRecord,
          saveRecord: saveRecord,
          deleteRecord: deleteRecord,
        };
      },
    },
    entity: { type: Object, default: () => {} },
    labelWidth: {
      type: Number,
      default: 75,
    },
    labelAlign: {
      type: String,
      default: "label-left-align",
    },
    field: { type: Object, default: () => {} },
    fieldProps: { type: Object, default: () => {} },
    fieldPropsMap: { type: Object, default: () => {} },
    formModel: {
      type: Object,
      default: () => {},
    },
    labelsModel: {
      type: Object,
      default: () => {},
    },
    formState: {
      type: Number,
      default: 0,
    },
    layout: { type: Object, default: () => {} },
  },
  data() {
    return {
      showReferenceDialogFlag: false,
      formWidgetLayout: {},
      formWidgetFormModel: {},
      formModelCopy: {},
      formWidgetLabelsModel: {},
      formWidgetFieldPropsMap: {},
      formWidgetFormState: {},
      curRefField: null,

      roleList: [], //权限角色列表

      options: [],

      uploadBtnHidden: false,
      fileList: [], //上传文件列表
      typeObj: {
        1: "Integer",
        2: "Decimal",
        3: "Text",
        4: "Textarea",
        5: "Option",
        6: "Tag",
        7: "Date",
        8: "DateTime",
        9: "Year",
        10: "Month",
        11: "Time",
        12: "File",
        13: "Picture",
      },
      tableName: "",
    };
  },
  computed: {
    SubformTitle() {
      // console.log(this.layout)
      return this.field && this.field.associated_subform
        ? this.field.associated_tablename_zh
        : "子表单";
      // return '子表单'
    },
    isReadOnly() {
      return (
        this.formState === FormState.PREVIEW ||
        this.formState === FormState.VIEW ||
        (this.formState === FormState.NEW && !this.fieldProps.creatable) ||
        (this.formState === FormState.EDIT && !this.fieldProps.updatable)
      );
    },

    fieldValue: {
      get() {
        //console.log('getValue: ' + this.field.name)
        // if (this.field.type === 'Boolean') {
        //   if (!this.formModel) {
        //     return null
        //   } else {
        //     if (isDefinedNull(this.formModel[this.field.name])) {
        //       return -1 //后台返回null值
        //     } else if (this.formModel[this.field.name] === true) {
        //       return 1 //后台返回true值
        //     } else {
        //       return 2 //后台返回false值
        //     }
        //   }
        // } else if (this.field.type === 'many') {
        //   if (this.formModel[this.field.name] == null) {
        //     return []
        //   }
        // } else if (
        //   !this.formModel ||
        //   isNull(this.formModel[this.field.name])
        // ) {
        //   return null //return ''
        // }

        if (this.field.type === 6) {
          let tagArray = [];
          let tagValuesStr = this.formModel[this.field.name]
            ? this.formModel[this.field.name] + ""
            : "";
          tagValuesStr.split(",").forEach((item) => {
            if (item.trim()) tagArray.push(item.trim());
          });
          return tagArray;
        } else if (this.field.type === 13 || this.field.type === 12) {
          //
          return this.formModel[this.field.name];
        } else if (!this.formModel || isNull(this.formModel[this.field.name])) {
          return "";
        } else {
          return this.formModel[this.field.name];
        }
      },
      set(newValue) {
        //console.log('setValue: ' + this.field.name + ', ' + newValue)
        if (this.formState !== 0) {
          /* 预览状态下formModel为null，下一行会报错！！ */

          if (this.field.type === "Boolean") {
            if (newValue === 1) {
              this.$emit("setFormModel", {
                key: this.field.name,
                value: true,
              });
              // this.formModel[this.field.name] = true
            } else if (newValue === 2) {
              // this.formModel[this.field.name] = false
              this.$emit("setFormModel", {
                key: this.field.name,
                value: false,
              });
            } else {
              this.$emit("setFormModel", {
                key: this.field.name,
                value: null,
              });
              // this.formModel[this.field.name] = null
            }
            return;
          }
          this.$emit("setFormModel", {
            key: this.field.name,
            value: newValue,
          });
          //赋值
          if (this.field.assignment_formula) {
            // [gj]=='ABW'～ [xb] != '0'#'9',
            // [gj]=='ABW'～ [xb] = '1',
            // [gj]=='ABW'～ ![dx2],
            // [gj]=='ABW'～ *[csrq],
            // [gj]=='AFG'～ ?[dx2]
            let formulas = this.field.assignment_formula.split(",");
            formulas.forEach((item) => {
              let arr = item.split("～");
              let condition = arr[0];
              let calculation = arr[1];

              const calculationField = calculation.match(/(?<=\[).*?(?=\])/gi);

              if (condition.includes("[")) {
                if (calculationField.length != 1) {
                  console.error("赋值公式错误，请重新修改！");
                  return;
                }
                const conditionFields = condition.match(/(?<=\[).*?(?=\])/gi);
                conditionFields.forEach((field, index) => {
                  if (this.formModel[field] == undefined) {
                    console.error("赋值公式错误，请重新修改！");
                    return;
                  }
                  condition = condition.replace(
                    `[${field}]`,
                    `'${this.formModel[field]}'`
                  );
                });
                if (calculation.includes("!=")) {
                  const value = calculation.split("!=")[1];
                  const values = [];
                  value.split("#").forEach((v) => {
                    values.push(v.trim());
                  });
                  if (calculationField[0]) {
                    this.$emit("setAttr", {
                      fields: calculationField,
                      attr: "disabledValues",
                      flag: eval(condition)?values.join(','):'',
                    });
                  } else {
                    console.error("联动公式错误，请修改！");
                  }
                } else if (calculation.includes("=")) {
                  const val = calculation.split("=")[1].trim();
                  if(eval(condition)){
                    this.$emit("setFormModel", {
                      key: calculationField[0],
                      value: val,
                    });
                  }

                } else if (calculation.includes("!")) {
                  this.$emit("setAttr", {
                    fields: calculationField,
                    attr: "is_edit",
                    flag: eval(condition) ? 0 : 1,
                  });
                } else if (calculation.includes("*")) {
                  this.$emit("setAttr", {
                    fields: calculationField,
                    attr: "is_null",
                    flag: eval(condition) ? 1 : 0,
                  });
                } else if (calculation.includes("?")) {
                  this.$emit("setAttr", {
                    fields: calculationField,
                    attr: "linkageImplication",
                    flag: eval(condition) ? 0 : 1,
                  });
                }
              } else {
                try {
                  let arr = calculation.split("=");
                  const fields = calculation.match(/(?<=\[).*?(?=\])/gi);
                  let condition = arr[1];
                  const assignmentField = fields[0];
                  const otherFields = fields.slice(1);

                  otherFields.forEach((field, index) => {
                    if (this.formModel[field] == undefined) {
                      console.error("联动公式错误，请修改！");
                      return;
                    }
                    condition = condition.replace(
                      `[${field}]`,
                      `'${this.formModel[field]}'`
                    );
                  });
                  this.$emit("setFormModel", {
                    key: assignmentField,
                    value: eval(condition),
                  });
                } catch (e) {
                  console.error("联动公式错误，请修改！");
                }
              }
            });
          }

          //逻辑联动
          // 'A'<[字段1]<'B'
          //     [字段1]<'A' &&[字段1]<'B'
          //     [字段1]>'A'
          //     [字段1]=='A'
          //     [字段1]=='A'||[字段1]=='B'
          //
          // ~
          //
          //     [字段1] != 1#2#3
          //     [字段1] = B
          //     *[字段1]必填
          //     ?[字段1]隐藏
          // ![字段1]禁用
          //
          //
          // 'A'<[字段1]<'B' ～ [字段1]!=1#2,[字段1]=='A'||[字段1]=='B' ~ ?[字段1]
        }
      },
    },

    fieldLabel: {
      get() {
        if (!this.labelsModel || isNull(this.labelsModel[this.field.name])) {
          return "";
        }

        return this.labelsModel[this.field.name];
      },
      set(newLabel) {
        console.log(newLabel);
