    addNewRecord(val) {
      if (val != this.field.association_value || !this.field.associated_subform)
        return;
      this.tableName = this.field.associated_table_name;
      const tableId = this.field.associated_table_id;
      this.crudMethods
        .createRecord({
          table_id: tableId,
          table_name: this.tableName,
        })
        .then((res) => {
          if (res.state == "error") {
            this.$message({ message: res.error, type: "error" });
            return;
          }
          if (!!res.t && !!res.t.layoutJson) {
            // this.curRecordId = null;
            this.formWidgetLayout = this.buildLayoutObj();
            this.formWidgetLayout.setLayoutPropsFromServer(res, "add");
            this.handleDeletedFields(res); /* 处理表单已删除字段！！ */
