    removeUploadFile(fileName) {
      let foundIdx = -1;
      this.fileList.forEach((file, idx) => {
        if (file.name === fileName) {
          foundIdx = idx;
        }
      });

      if (foundIdx >= 0) {
        this.fileList.splice(foundIdx, 1);

        let fileNamesStr = "";
        this.fileList.forEach((item) => {
          if (!isEmptyStr(item.name)) {
            fileNamesStr += item.name + "||";
          }
        });
        // this.formModel[this.field.name] = fileNamesStr
        this.uploadBtnHidden = this.fileList.length >= this.maxFileCount;
      }
    },

    handleAdd(filed, columns, fieldValue) {
      if (!Array.isArray(this.fieldValue)) {
        this.fieldValue = fieldValue = [];
      }
      setTimeout(() => {
        console.log(this.fieldValue);
        const json = {};
        columns.forEach((obj) => {
          const name = obj.prop;
          json[name] = "";
        });
        console.log(this.fieldValue);
        if (Array.isArray(this.fieldValue)) {
          this.fieldValue.push(json);
        } else {
          let arr = [];
          arr.push(json);
          this.fieldValue = arr;
        }
      });
    },
    handleUp(index) {
      let m = this.fieldValue.splice(index, 1)[0];
      this.fieldValue.splice(index - 1, 0, m);
    },
    handleDel(index) {
      this.fieldValue.splice(index, 1);
    },
    handleDown(index) {
      let m = this.fieldValue.splice(index, 1)[0];
      this.fieldValue.splice(index + 1, 0, m);
    },
  },
};
</script>

